<template>
  <b-card title="">
    <div class="custom-search">
      <!-- advance search input -->
      <b-row class="align-items-end">
        <b-col md="12">
          <b-form-group>
            <b-button
              v-ripple.400="'rgba(255, 255, 255, 0.15)'"
              type="button"
              variant="primary"
              class="float-right"
              exact
              :to="{ name: 'add-promotion' }"
            >
              <feather-icon icon="PlusIcon" class="mr-50" />
              <span class="align-middle"></span>
            </b-button>
          </b-form-group>
        </b-col>
      </b-row>
    </div>

    <vue-good-table
      mode="remote"
      :columns="columns"
      :rows="rows"
      :total-rows="totalRecords"
      :rtl="direction"
      :is-loading.sync="isLoading"
      :pagination-options="{
        enabled: true,
      }"
      @on-sort-change="onSortChange"
    >
      <template slot="table-row" slot-scope="props">
        <!-- Column: Action -->
        <span v-if="props.column.field === 'lottoType'">
          <img :src="props.row.lotto_type_img" alt="" width="35" />
          &nbsp;
          <span>{{ props.row.lotto_type_name }}</span>
        </span>
        <!-- <span v-if="props.column.field === 'status_prize'">
          <span class="badge bg-success" v-if="props.row.active">
            
          </span>
          <span class="badge bg-danger" v-else>  </span>
        </span> -->
        <span v-if="props.column.field === 'num'">
          <!-- <button>{{ props.row.is_active}}</button> -->
          <span>{{ props.row.originalIndex + 1 }}</span>
        </span>
        <span v-if="props.column.field === 'date_prize'">
          <span>{{ formatDate(props.row.closing_time) }}</span>
        </span>
        <!-- <span v-if="props.column.field === 'status_active'">
          <b-form-checkbox
            unchecked-value="0"
            name="check-button"
            class="custom-control-success"
            switch
            inline
            checked
            value="1"
            @change="updateStatus(props.row.lotto_type_id)"
            v-model="props.row.active"
          >
          </b-form-checkbox>
        </span> -->
        <span v-if="props.column.field === 'action'">
          <span>
            <b-dropdown
              variant="link"
              toggle-class="text-decoration-none"
              no-caret
            >
              <template v-slot:button-content>
                <feather-icon
                  icon="MoreVerticalIcon"
                  size="16"
                  class="text-body align-middle mr-25"
                />
              </template>
              <!-- <b-dropdown-item
                :to="{
                  name: 'edit-promotion',
                  params: { id: props.row.promotion_id },
                }"
              >
                <feather-icon icon="Edit2Icon" class="mr-50" />
                <span>Edit</span>
              </b-dropdown-item> -->
              <b-dropdown-item
                @click="deletePromotion(props.row.promotion_id)"
              >
                <feather-icon icon="TrashIcon" class="mr-50" />
                <span>Delete</span>
              </b-dropdown-item>
            </b-dropdown>
          </span>
        </span>

        <!-- Column: Common -->
        <span v-else>
          {{ props.formattedRow[props.column.field] }}
        </span>
      </template>

      <!-- pagination -->
      <template slot="pagination-bottom" slot-scope="props">
        <div class="d-flex justify-content-between flex-wrap">
          <!-- page length -->
          <div class="d-flex align-items-center mb-0 mt-1">
            <span class="text-nowrap">  </span>
            <b-form-select
              v-model="pageLength"
              :options="pages"
              class="mx-1"
              @input="handlePageChange"
            />
            <span class="text-nowrap">  {{ props.total }}  </span>
          </div>

          <!-- pagination -->
          <div>
            <b-pagination
              :value="1"
              :total-rows="props.total"
              :per-page="pageLength"
              first-number
              last-number
              align="right"
              prev-class="prev-item"
              next-class="next-item"
              class="mt-1 mb-0"
              @change="handleChangePage"
            >
              <template #prev-text>
                <feather-icon icon="ChevronLeftIcon" size="18" />
              </template>
              <template #next-text>
                <feather-icon icon="ChevronRightIcon" size="18" />
              </template>
            </b-pagination>
          </div>
        </div>
      </template>

      <template slot="loadingContent"> Loading </template>
    </vue-good-table>
  </b-card>
</template>

<script>
import {
  BPagination,
  BFormGroup,
  BFormSelect,
  BDropdownItem,
  BDropdown,
  BRow,
  BCol,
  BButton,
  BCard,
  BFormInput,
  BModal,
  BFormTextarea,
  BForm,
} from "bootstrap-vue";
import { VueGoodTable } from "vue-good-table";
import Ripple from "vue-ripple-directive";
import store from "@/store/index";
import { HTTP } from "@/services/http-common";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import { BFormCheckbox } from "bootstrap-vue";
import vSelect from "vue-select";
import { ValidationProvider, ValidationObserver } from "vee-validate";
import { required, integer } from "@validations";
import moment from "moment";

export default {
  components: {
    VueGoodTable,
    BPagination,
    BFormGroup,
    BFormInput,
    BFormSelect,
    BDropdownItem,
    BDropdown,
    BCol,
    BRow,
    BButton,
    BCard,
    BFormCheckbox,
    BModal,
    BFormTextarea,
    BForm,
    vSelect,
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      isLoading: false,
      currentPage: 1,
      pageLength: 10,
      totalRecords: 0,
      sort: [
        {
          field: "",
          sort: "",
        },
      ],
      dir: false,
      pages: ["5", "10", "50", "100"],
      columns: [
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "",
          field: "num",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "",
          field: "promotion_name",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 2  (%)",
          field: "discount_2top",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 2  (%)",
          field: "discount_2bottom",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 3  (%)",
          field: "discount_3top",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 3  (%)",
          field: "discount_3tod",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "  (%)",
          field: "discount_runtop",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "  (%)",
          field: "discount_runbottom",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 2  ()",
          field: "pay_2top",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 2  ()",
          field: "pay_2bottom",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 3  ()",
          field: "pay_3top",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: " 3  ()",
          field: "pay_3tod",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "  ()",
          field: "pay_runtop",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "  ()",
          field: "pay_runbottom",
        },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "",
          field: "date_prize",
        },
        // {
        //   thClass: "text-center",
        //   tdClass: "text-center",
        //   label: "",
        //   field: "status_prize",
        // },
        // {
        //   thClass: "text-center",
        //   tdClass: "text-center",
        //   label: "/",
        //   field: "status_active",
        // },
        {
          thClass: "text-center",
          tdClass: "text-center",
          label: "",
          field: "action",
          sortable: false,
          width: "64px",
        },
      ],
      rows: [],
      form: {
        prize3top: "",
        prize2bottom: "",
        typeHuay: "",
        type: "",
        type3top: "3 ",
        type2bottom: "2 ",
      },
      required,
      integer,
      dataCredit: "",
      dataDisCredit: "",
      max: 3,
    };
  },
  computed: {
    direction() {
      if (store.state.appConfig.isRTL) {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.dir = true;
        return this.dir;
      }
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      this.dir = false;
      return this.dir;
    },
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.isLoading = true;
      HTTP.get("api/promotion")
        .then((response) => {
          this.rows = response.data.data;
          this.totalRecords = response.data.data.length;
          this.isLoading = false;
        })
        .catch((error) => {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text:
                error.response.data.msg !== undefined
                  ? error.response.data.msg
                  : error.status,
              icon: "XCircleIcon",
              variant: "danger",
            },
          });
          this.isLoading = false;
        });
    },
    updateStatus(id) {
      HTTP.get(`api/lotto-type/all/active-type`, {
        params: { lotto_type_id: id },
      })
        .then((resp) => {
          this.showToast(resp.data.msg, "success");
          this.getData();
        })
        .catch((err) => {
          if (err.response) {
            if (err.response.status === 403) {
              this.$store.dispatch("logoutAuth");
              this.$router.push("/login");
            } else {
              this.showToast(
                err.response.data.msg !== undefined
                  ? err.response.data.msg
                  : " ",
                "danger"
              );
              this.getData();
            }
          }
        });
    },
    deletePromotion(id) {
      HTTP.delete(`api/promotion/del-promotion`, {
        data: { promotion_id: id },
      })
        .then((resp) => {
          this.showToast(resp.data.msg, "success");
          this.getData();
        })
        .catch((err) => {
          if (err.response) {
            if (err.response.status === 403) {
              this.$store.dispatch("logoutAuth");
              this.$router.push("/login");
            } else {
              this.showToast(
                err.response.data.msg !== undefined
                  ? err.response.data.msg
                  : " ",
                "danger"
              );
              this.getData();
            }
          }
        });
    },
    handleChangePage(page) {
      this.currentPage = page;
      this.getData();
    },
    handlePageChange(active) {
      this.perPage = active;
      this.getData();
    },
    onSortChange(params) {
      this.sort[0].field = params[0].field;
      this.sort[0].sort = params[0].type;
      this.getData();
    },
    showToast(text, variant) {
      this.$toast({
        component: ToastificationContent,
        position: "top-right",
        props: {
          title: "...",
          text: text,
          icon: variant === "success" ? "CheckIcon" : "BellIcon",
          variant: variant,
        },
      });
    },
    formatDate(date) {
      return moment(date).format("DD-MM-YYYY HH:mm:ss");
    },
  },
};
</script>
<style lang="scss">
@import "@core/scss/vue/libs/vue-good-table.scss";
</style>
<style lang="scss">
@import "@core/scss/vue/libs/vue-select.scss";
</style>
